@import '../../../assets/styles/mixins';

.questions-container {
  height: 100vh;
  display: flex;
  .questions {
    width: 40rem;
    margin: auto;
    padding: 60px;
    text-align: center;
    box-shadow: 0px 0px 20px -10px rgba(0, 0, 0, 0.75);

    @include for-phone {
      width: 100%;
      padding: 20px;
    }
  }
}

.questions-buttons {
  width: 100%;
  justify-content: space-evenly;
  display: flex;

  @include for-phone {
    flex-direction: column;
  }
  .next {
    width: 15rem;
    @include for-phone {
      width: 80%;
      margin: 0.5rem auto 0.5rem auto;
    }
  }
  .back {
    width: 15rem;
    @include for-phone {
      width: 80%;
      margin: 0.5rem auto 0.5rem auto;
    }
  }
}
